import 'core-js';

import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';
import axios from 'axios';
//import fetchJsonp from 'fetch-jsonp';


import Home from './panels/Home';
import Persik from './panels/Persik';

const app_id = 7177213;
const group_id = 148056070;
connect.send("VKWebAppInit", {});


function  hash(){
	try
    {
        let encoded = decodeURIComponent(window.location.hash.split("#")[1]);
        console.log({encoded});
        return JSON.parse(encoded);
    }catch(err){
		console.log(err);
	}
}






const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [getToken, setToken] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);

    console.log("421");

	useEffect(() => {

		connect.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}

            if (type === 'VKWebAppAccessTokenReceived') {
                setToken(data.access_token);
                window.token = data.access_token;




            }
		});



        connect.send("VKWebAppGetAuthToken", {"app_id": app_id, "scope": "wall,photos"});

		async function fetchData() {
			const user = await connect.sendPromise('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);



	async function PayloadStorageUpload(payload) {
		let random = new Date().getTime()+Math.floor(Math.random()*100000);
        payload.random = random;

        /*Отправляем в группу с нашим random*/
        connect.send("VKWebAppSendPayload", {"group_id": group_id, payload});

        /*Ждем записи с нашим random*/
        return await new Promise((r, j)=>{
        	let i = 1;
           async function check() {
           		i++;
                console.warn('checking',i);
                let storage_get = await connect.sendPromise("VKWebAppCallAPIMethod",{"method": "storage.get", "request_id": random, "params": {"v":"5.102", "access_token":window.token, "key":"upload"}});
				let json = JSON.parse(storage_get.response);
                if(json.random === random) r(json);
                else if(i>20) j("Result not finded!");
                else
                    setTimeout(check, 350)
            }
            check();
        });
    }

    async function poster() {
        console.log("ppposer21");
        console.log(hash());
        let upload_server = await connect.sendPromise("VKWebAppCallAPIMethod",{"method": "photos.getWallUploadServer", "request_id": "32test", "params": {"v":"5.102", "access_token":window.token}});console.log({upload_server});
        let {upload_url, user_id} = upload_server.response;
		let upload = await PayloadStorageUpload({upload_url, user_id, photo:hash()});console.log({upload})
        let save = await connect.sendPromise("VKWebAppCallAPIMethod",{"method": "photos.saveWallPhoto", "request_id": "33test", "params": {"v":"5.102", "access_token":getToken, "user_id":user_id, photo:upload.photo, hash:upload.hash, server:upload.server, caption:"@art_privet(Арт за 5 сек)"}});
        let {id,owner_id} = save.response[0];
        let attachments = "photo"+owner_id+"_"+id;
        const post = await connect.sendPromise("VKWebAppShowWallPostBox",{"message":"@art_privet(Арт за 5 сек)",attachments});
    };




	return (
		<View activePanel={activePanel} popout={popout}>
			<Home id='home' fetchedUser={fetchedUser} go={poster} hash={hash()} />
		</View>
	);
}

export default App;

